.account-details-container {
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  display: flex;
  justify-content: space-between;
  align-items: center;
  opacity: 1;
  background: "transparent";
  color: rgb(52, 71, 103);
  box-shadow: none;
}

.card-container {
  width: 100%;
  color: rgb(52, 71, 103);
  background-color: rgb(255, 255, 255);
  background-clip: border-box;
  border-radius: 0.75rem;
}

.card-content {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.header-container,
.form-container>div:first-child,
.form-container>div:nth-child(2),
.form-container>div:nth-child(3),
.form-container>div:nth-child(4) {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 25px;
}

.form-container h5 {
  font-size: 0.875rem;
  font-weight: 700;
  letter-spacing: 0.02857em;
}

.form-container>div input {
  color: rgb(52, 71, 103);
  align-items: baseline;
}

.header-container h5 {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.25rem;
  line-height: 1.375;
  font-weight: 700;
  gap: 15px;
  text-transform: capitalize;
  color: rgb(52, 71, 103);
}

.header-container a {
  color: rgb(52, 71, 103);
  font-size: 1rem;
  font-weight: 300;
}

.form-container {
  width: 100%;
  gap: 20px;
}

.form-container p {
  font-weight: 400;
  font-size: 0.9rem;
  color: #9296ac;
}

.tooltip-container {
  display: flex;
  align-items: center;
}

.public-profile {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

.btn-container {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  padding-right: 5px;
}

button[type="submit"] {
  color: white;
}

.container-confirmation {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 30px;
  padding-bottom: 5px;
  width: 100%;
  justify-content: flex-start;
}